* {
    /* change the default since I haven't gotten to accounting borders yet*/
    box-sizing: border-box;
}

#container {
    border: 3px solid #6495ED;
    display: inline-flex;
    flex-direction: column;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    border-radius: 12px;
    padding: 15px 15px;
}

#colors {
    color: unset;
}

.boxes {
    float: left;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

#container > div {
    display: flex;
    flex-direction: row;
}

.versions {
    flex-grow: 3;
}

.boxes > h2 {
    text-align: center;
}

#panel {
    font-weight: bold
}

textarea {
    flex-grow: 1;
    min-width: 100%;
    max-width: fit-content;
}

#row2 > * {
    margin: auto;
}

#panel > * {
    margin-block: 1%;
}

#panel > div {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

#export > textarea {
    margin-block: 1%;
    min-width: 100%;
    resize: both;
}

#panel, button {
    align-self: center;
    text-align: center;
}

/* Left over from react creation, but still using */
.App {
    text-align: center;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    /*font-size: calc(10px + 2vmin);*/
    color: white;
}

.App-link {
    color: #61dafb;
}
